var countDown = 2;
var app = getApp();
export default {
	data() {
		return {
			pintuanData: '',
			pintuan_home_id: '',
			'videoPath': '',
			'videoStatus': 2,
			cdnUrl: app.globalData.cdnUrl,
			userinfo: '',
			options: '',
		}
	},
	onLoad(options) {
		var that = this;
		that.options = options;
		that.userinfo = uni.getStorageSync('userinfo') ? uni.getStorageSync('userinfo') : uni.getStorageSync('userInfo')?uni.getStorageSync('userInfo'):'';
		console.log('userinfo===', that.userinfo);
		if (options.pintuan_home_id && options.action) {
			that.pintuan_home_id = options.pintuan_home_id;
			that.action = options.action;
			that.getPintuan();
		}
	},
	onHide() {
		this.countDown = 1;
	},
	onUnload() {
		this.countDown = 1;
	},
	onShow() {
		if (this.pintuanData != '' && this.countDown == 1) {
			this.getPintuan();
		}
		this.countDown = 2;
	},
	//监听用户下拉动作，一般用于下拉刷新
	onPullDownRefresh() {
		var that = this;
		this.countDown = 1;
		that.getPintuan();

	},
	methods: {

		// 获取视频说明地址
		getShuomingVideo() {
			var that = this;
			console.log('获取视频说明地址')
			if (that.videoPath != '') {
				that.videoStatus = 1;
				return;
			}
			this.$http.post('getVideoPath', {
				id: that.pintuanData.rule_video
			}).then((res) => {
				console.log('视频地址', res);
				that.videoPath = res.data;
			})
			that.videoStatus = 1;
		},
		closeVideo() {
			var that = this;
			if (that.videoStatus == 1) {
				that.videoStatus = 2;
			} else {
				that.videoStatus = 1;
			}
		},
		getPintuan() {
			var that = this;
			this.$http.post("pintuan/show", {
				pintuan_id: that.pintuan_home_id,
				action: that.action
			}).then((res) => {
				that.pintuanData = res.data;
				setTimeout(function() {
					that.setCountDown();
				}, 500)
			})
		},
		onGotUserInfo(res) {
			var that = this
			console.log("getuserinfo===", res);
			if (res.detail.userInfo) {
				uni.setStorageSync('userInfo', res.detail.userInfo);
				this.upUserInfo(res.detail);

				this.chaiHB();
				var jumptype = res.currentTarget.dataset.jumptype;
				var url = res.currentTarget.dataset.url;
				var closepage = res.currentTarget.dataset.closepage;
				this.jumpFunction(jumptype, url, closepage);


			}
		},
		chaiHB() {
			var that = this;
			if (this.options.hid) {
				this.$http.post("chaiHB", {
					hid: that.options.hid
				}, {
					'showErrorMsg': false
				}).then((res) => {
					console.log('助力', res);
				})
			}
		},
		jumpTo(e) {
			this.chaiHB();

			var jumptype = e.currentTarget.dataset.jumptype;
			var url = e.currentTarget.dataset.url;
			var closepage = e.currentTarget.dataset.closepage;
			this.jumpFunction(jumptype, url, closepage);
		},
		// 一个倒计时
		setCountDown() {
			var that = this;
			var time = 1000;
			var datas = this.pintuanData;
			if (datas.times <= 0) {
				datas.times = 0;
			}
			datas.times = datas.times * 1000;
			let formatTime = this.getFormat(datas.times);
			datas.times -= 1000;
			datas.h = `${formatTime.hh}`;
			datas.m = `${formatTime.mm}`;
			datas.s = `${formatTime.ss}`;
			datas.times = datas.times / 1000;
			that.pintuanData = datas;
			// console.log(datas.s);
			if (that.countDown == 2) {
				setTimeout(this.setCountDown, time);
			}

		},
	}
}
